body,
html {
  margin: 0;
  padding: 0;
  border: 0;
  overflow: hidden;
  height: 100%;
  max-height: 100%;
}


.controller {
  /* position: absolute; */
  /* width: 24px;
  height: 12px; */
  /* z-index: 100; */
  /* bottom: 20px;
  left: 50%; */
  /* margin-left: -12px; */
  /* margin-top: 1.5rem; */
  -webkit-animation: bounce-down 1.6s linear infinite;
  animation: bounce-down 1.6s linear infinite;

}

@-webkit-keyframes bounce-down {
 25% {
      -webkit-transform: translateY(-4px);
 }
 50%, 100% {
      -webkit-transform: translateY(0);
 }
 75% {
      -webkit-transform: translateY(4px);
 }
}

@keyframes bounce-down {
  25% {
       -webkit-transform: translateY(-4px);
               transform: translateY(-4px);
  }
  50%, 100% {
       -webkit-transform: translateY(0);
               transform: translateY(0);
  }
  75% {
       -webkit-transform: translateY(4px);
               transform: translateY(4px);
  }
}

#smartImageProject {
  max-width: 35rem;
  height: 9rem;
  position: relative;
  border-radius: 65px;
  -webkit-filter: grayscale(100%);
          filter: grayscale(100%);
}

#smartImageProject:hover {
  -webkit-filter: grayscale(60%);
          filter: grayscale(60%);
}

.centerTextProject {
  position: absolute;
  top: 50%;
  left: 19%;
  width: 100%;
  z-index: 99;
}

.hoverBlur {
  /* filter: blur(4px); */
  border-radius: 65px;
  transition: -webkit-transform 0.2s;
  transition: transform 0.2s;
  transition: transform 0.2s, -webkit-transform 0.2s; /* Animation */
  cursor: pointer;
  margin-left: 1rem;
  margin-right: 1rem;
}

.hoverBlur:hover {
  /* filter: none; */
  -webkit-transform: scale(0.9);
          transform: scale(0.9);
  cursor: pointer;
}

